import React, { Component } from 'react';
import testTodoListData from './TestTodoListData.json'
import HomeScreen from './components/home_screen/HomeScreen'
import ItemScreen from './components/item_screen/ItemScreen'
import ListScreen from './components/list_screen/ListScreen'
import EditScreen from './components/item_screen/EditScreen'

import { jTPS } from './lib/jTPS'

import ReactDOM from 'react-dom'


const AppScreen = {
  HOME_SCREEN: "HOME_SCREEN",
  LIST_SCREEN: "LIST_SCREEN",
  ITEM_SCREEN: "ITEM_SCREEN",
  EDIT_SCREEN: "EDIT_SCREEN"
}



class App extends Component {
  state = {
    currentScreen: AppScreen.HOME_SCREEN,
    todoLists: testTodoListData.todoLists,
    currentList: null,
    currentItem: null,

    jsTPS_UndoStack: [],
    jsTPS_RedoStack: [],
    action: [],

    jTPS: new jTPS()


  }



  goHome = () => {
    this.setState({ currentScreen: AppScreen.HOME_SCREEN });
    this.setState({ currentList: null });


    this.setState({ jsTPS_RedoStack: [] });
    this.setState({ jsTPS_UndoStack: [] });
    this.setState({ action: [] });
    this.setState({ jTPS: new jTPS() });

    var temp = this.state.todoLists;

    temp[this.state.currentList.key] = this.state.currentList;

    this.setState({ todoListList: temp });

  }

  loadList = (todoListToLoad) => {
    this.setState({ currentScreen: AppScreen.LIST_SCREEN });
    this.setState({ currentList: todoListToLoad });
    console.log("currentList: " + this.state.currentList);
    console.log("currentScreen: " + this.state.currentScreen);
  }

  loadNewList = () => {
    this.setState({ currentScreen: AppScreen.LIST_SCREEN });
    let todoList = {
      "key": this.state.todoLists.length,
      "name": "Unknown",
      "owner": "",
      "items": []
    };
    this.setState({ currentList: todoList });
    this.state.todoLists.push(todoList);
  }

  loadItem = () => {
    this.setState({ currentScreen: AppScreen.ITEM_SCREEN });
  }

  editItem = (itemPointer) => {
    this.setState({ currentScreen: AppScreen.EDIT_SCREEN });
    this.setState({ currentItem: itemPointer })
  }

  escFunction = (event) => {
    var undoStack = this.state.jsTPS_UndoStack;
    var redoStack = this.state.jsTPS_RedoStack;
    var action = this.state.action;
    if (event.keyCode === 90 && event.ctrlKey) {

      if (this.state.currentScreen == 'LIST_SCREEN' && this.state.jTPS.hasTransactionToUndo()) {
        // console.log(this.state.todoLists);
        let result = this.state.jTPS.undoTransaction();

        this.setState({ currentList: result })
        this.loadList(this.state.currentList);
        var txtBox = document.getElementById("list_name_textfield");
        txtBox.value = this.state.currentList.name;
        txtBox = document.getElementById("list_owner_textfield");
        txtBox.value = this.state.currentList.owner;


      }

    }
    else if (event.keyCode === 89 && event.ctrlKey) {
      if (this.state.currentScreen == 'LIST_SCREEN' && this.state.jTPS.hasTransactionToRedo()) {

        let result = this.state.jTPS.doTransaction();

        this.setState({ currentList: result })
        this.loadList(this.state.currentList);

        this.loadList(this.state.currentList);
        var txtBox = document.getElementById("list_name_textfield");
        txtBox.value = this.state.currentList.name;
        txtBox = document.getElementById("list_owner_textfield");
        txtBox.value = this.state.currentList.owner;
      }
    }

  }
  componentDidMount() {
    document.addEventListener("keydown", this.escFunction, false);
  }

  render() {
    // if (this.state.intial == false) {
    //   var copy = JSON.parse(JSON.stringify(this.state.todoLists));
    //   this.state.jsTPS_UndoStack.push(copy);
    //   this.setState({ intial: true });
    // }

    switch (this.state.currentScreen) {
      case AppScreen.HOME_SCREEN:
        return <HomeScreen
          loadList={this.loadList.bind(this)}  //function
          todoLists={this.state.todoLists}  //var
          loadNewList={this.loadNewList.bind(this)}

        // a={this.escFunction.bind(this)}
        // currentScreen={this.state.currentScreen}
        // escFunction={this.escFunction.bind(this)}
        // componentDidMount={this.componentDidMount.bind(this)}
        />;
      case AppScreen.LIST_SCREEN:
        return <ListScreen
          goHome={this.goHome.bind(this)}
          todoList={this.state.currentList}
          loadList={this.loadList.bind(this)}
          homeList={this.state.todoLists}
          loadItem={this.loadItem.bind(this)}
          editItem={this.editItem.bind(this)}

          jtps={this.state.jTPS}

          undo={this.state.jsTPS_UndoStack}
          redo={this.state.jsTPS_RedoStack}
          curr={this.state.currentList}
          action={this.state.action}
        />
      case AppScreen.ITEM_SCREEN:
        return <ItemScreen
          todoList={this.state.currentList}
          loadList={this.loadList.bind(this)}

          homeList={this.state.todoLists}
          undo={this.state.jsTPS_UndoStack}
          redo={this.state.jsTPS_RedoStack}
          curr={this.state.currentList}
          jtps={this.state.jTPS} />;
      case AppScreen.EDIT_SCREEN:
        return <EditScreen
          todoList={this.state.currentList}
          loadList={this.loadList.bind(this)}
          currentItem={this.state.currentItem}

          homeList={this.state.todoLists}
          undo={this.state.jsTPS_UndoStack}
          redo={this.state.jsTPS_RedoStack}
          curr={this.state.currentList}
          action={this.state.action}
          jtps={this.state.jTPS}
        />;
      default:
        return <div>ERROR</div>;
    }
  }
}



export default App;