import React, { Component } from 'react'

export class ListModal extends Component {
    render() {
        return (

            <div id="modal_yes_no_dialog" >
                <p>&ensp;&ensp;Delete list?</p>
                <br></br>
                <p><b>&ensp;&ensp;Are you sure you want to delete this list?&ensp;&ensp;</b></p>
                <input type="button" id="dig_yes" value="Yes" />
                <input type="button" id="dig_no" value="No" onClick={() => hideDiag()} />
                <br></br>
                <p>&ensp;&ensp;The list will not be retrievable</p>
            </div>
        )
    }
}

function hideDiag() {
    let dialog = document.getElementById('modal_yes_no_dialog');
    dialog.style.animation = 'example2 1s 1';
    setTimeout(hideDialog2, 1000);
}
function hideDialog2() {
    let dialog = document.getElementById('modal_yes_no_dialog');
    dialog.hidden = true;
}

export default ListModal
