export class jTPS {

    constructor() {
        this.transactions = [];
        this.mostRecentTransaction = -1;
        this.performingDo = false;
        this.performingUndo = false;
    }

    isPerformingDo() {
        return this.performingDo;
    }

    isPerformingUndo() {
        return this.performingUndo;
    }

    hasTransactionToUndo() {
        return (this.mostRecentTransaction >= 0);
    }

    hasTransactionToRedo() {
        return (this.mostRecentTransaction < (this.transactions.length - 1));
    }

    getUndoSize() {
        return this.mostRecentTransaction + 1;
    }

    getSize() {
        return this.transactions.length;
    }

    getRedoSize() {
        return this.getSize() - this.mostRecentTransaction - 1;
    }

    peekUndo() {
        if (this.hasTransactionToUndo()) {
            return this.transactions[this.mostRecentTransaction];
        }
        else
            return null;
    }

    clearAllTransactions() {
        this.transactions = [];
        this.mostRecentTransaction = -1;
    }
    peekDo() {
        if (this.hasTransactionToRedo()) {
            return this.transactions[this.mostRecentTransaction + 1];
        }
        else
            return null;
    }

    undoTransaction() {
        if (this.hasTransactionToUndo()) {
            this.performingUndo = true;
            transaction = this.transactions[this.mostRecentTransaction];
            let result = transaction.undoTransaction();
            this.mostRecentTransaction--;
            this.performingUndo = false;
            return result;
        }
    }


    doTransaction() {
        if (this.hasTransactionToRedo()) {
            this.performingDo = true;
            let transaction = this.transactions[this.mostRecentTransaction + 1];
            let result = transaction.doTransaction();
            this.mostRecentTransaction++;
            this.performingDo = false;
            return result;
        }
    }
    addTransaction(transaction) {
        if ((this.mostRecentTransaction < 0) || (this.mostRecentTransaction < (this.transactions.length - 1))) {
            for (let i = this.transactions.length - 1; i > this.mostRecentTransaction; i--) {
                this.transactions.splice(i, 1);
            }
        }
        this.transactions.push(transaction);
        this.doTransaction();
    }
}


class transaction {
    doTransaction() {
    }
    undoTransaction() {
    }
}
