export default class transaction {
    constructor(old, neww, currentList) {
        this.old = old;
        this.neww = neww;
        this.currentList = currentList;
    }
    doTransaction() {
        this.currentList = this.neww;
        return this.currentList;
    }
    undoTransaction() {
        this.currentList = this.old;
        return this.currentList;
    }
}