import React, { Component } from 'react'
import ListItemCard from './ListItemCard'
import addButton from '../images/icons/AddItem.png'
export class ListItemsTable extends Component {

    state = {
        taskInc: false,
        dueDateInc: false,
        compInc: false
    }

    sortTask = (itemList) => {
        if (this.state.taskInc) {
            this.setState({ taskInc: false });
            var n = itemList.length;
            for (var i = 0; i < n - 1; i++) {
                for (var j = 0; j < n - i - 1; j++) {
                    if (itemList[j].description < itemList[j + 1].description) {
                        var t = itemList[j];
                        itemList[j] = itemList[j + 1];
                        itemList[j + 1] = t;

                        var k = itemList[j].key;
                        itemList[j].key = itemList[j + 1].key;
                        itemList[j + 1].key = k;

                    }
                }
            }
        }
        else {
            this.setState({ taskInc: true });
            var n = itemList.length;
            for (var i = 0; i < n - 1; i++) {
                for (var j = 0; j < n - i - 1; j++) {
                    if (itemList[j].description > itemList[j + 1].description) {
                        var t = itemList[j];
                        itemList[j] = itemList[j + 1];
                        itemList[j + 1] = t;

                        var k = itemList[j].key;
                        itemList[j].key = itemList[j + 1].key;
                        itemList[j + 1].key = k;
                    }
                }
            }
        }
    }



    sortDueDate = (itemList) => {
        if (this.state.compInc) {
            this.setState({ compInc: false });
            var n = itemList.length;
            for (var i = 0; i < n - 1; i++) {
                for (var j = 0; j < n - i - 1; j++) {
                    if (itemList[j].due_date < itemList[j + 1].due_date) {
                        var t = itemList[j];
                        itemList[j] = itemList[j + 1];
                        itemList[j + 1] = t;

                        var k = itemList[j].key;
                        itemList[j].key = itemList[j + 1].key;
                        itemList[j + 1].key = k;
                    }
                }
            }
        }
        else {
            this.setState({ compInc: true });
            var n = itemList.length;
            for (var i = 0; i < n - 1; i++) {
                for (var j = 0; j < n - i - 1; j++) {
                    if (itemList[j].due_date > itemList[j + 1].due_date) {
                        var t = itemList[j];
                        itemList[j] = itemList[j + 1];
                        itemList[j + 1] = t;

                        var k = itemList[j].key;
                        itemList[j].key = itemList[j + 1].key;
                        itemList[j + 1].key = k;
                    }
                }
            }
        }
    }

    sortComp = (itemList) => {
        if (this.state.dueDateInc) {
            this.setState({ dueDateInc: false });
            var n = itemList.length;
            for (var i = 0; i < n - 1; i++) {
                for (var j = 0; j < n - i - 1; j++) {
                    if (itemList[j].completed < itemList[j + 1].completed) {
                        var t = itemList[j];
                        itemList[j] = itemList[j + 1];
                        itemList[j + 1] = t;

                        var k = itemList[j].key;
                        itemList[j].key = itemList[j + 1].key;
                        itemList[j + 1].key = k;
                    }
                }
            }
        }
        else {
            this.setState({ dueDateInc: true });
            var n = itemList.length;
            for (var i = 0; i < n - 1; i++) {
                for (var j = 0; j < n - i - 1; j++) {
                    if (itemList[j].completed > itemList[j + 1].completed) {
                        var t = itemList[j];
                        itemList[j] = itemList[j + 1];
                        itemList[j + 1] = t;

                        var k = itemList[j].key;
                        itemList[j].key = itemList[j + 1].key;
                        itemList[j + 1].key = k;
                    }
                }
            }
        }
    }


    render() {


        //console.log(' table func ' + this.props.loadFunc)
        return (
            <div id="list_items_container">
                <div className='list_item_header_card' >
                    <div className="list_item_task_header" onClick={() => this.sortTask(this.props.todoList.items)}>Task</div>
                    <div className="list_item_due_date_header" onClick={() => this.sortDueDate(this.props.todoList.items)}>Due Date</div>
                    <div className="list_item_status_header" onClick={() => this.sortComp(this.props.todoList.items)}>Status</div>
                </div>
                {
                    this.props.todoList.items.map((todoItem) => (
                        <ListItemCard
                            key={todoItem.key}
                            listItem={todoItem}
                            length={this.props.todoList.items.length}
                            list={this.props.todoList}
                            loadFunc={this.props.loadFunc}
                            editItemFunc={this.props.editItem}

                            undo={this.props.undo}
                            redo={this.props.redo}
                            homeList={this.props.homeList}
                            curr={this.props.curr}
                            action={this.props.action}

                            jtps={this.props.jtps}
                        />
                    ))
                }
                <div className='list_item_add_card' onClick={() => loadItemScreen(this.props.loadItem)}>
                    <img src={addButton} /><img />
                </div>

            </div>
        )
    }
}

function loadItemScreen(func) {
    func();
}

export default ListItemsTable
