import React, { Component } from 'react'
import PropTypes from 'prop-types';
import transaction from '../../lib/transaction';

export class ItemScreen extends Component {

    go2List() {
        console.log(this.props.todolist);
        // alert(this.props.todoList);
        this.props.loadList(this.props.todoList);

    }

    edit2List() {
        //var Description = document.getElementById('item_description_textfield').value;


        // let newItem = {
        //     "assigned_to": document.getElementById('item_assigned_to_textfield').value,
        //     "completed": document.getElementById('item_completed_checkbox').checked,
        //     "description": document.getElementById('item_description_textfield').value,
        //     "due_date": document.getElementById('item_due_date_picker').value,
        //     "key": this.props.todoList.items.length
        // };
        var old = JSON.parse(JSON.stringify(this.props.curr));
        this.props.currentItem.description = document.getElementById('item_description_textfield').value;
        this.props.currentItem.assigned_to = document.getElementById('item_assigned_to_textfield').value;
        this.props.currentItem.due_date = document.getElementById('item_due_date_picker').value;
        this.props.currentItem.completed = document.getElementById('item_completed_checkbox').checked;


        // this.props.todoList.items.push(newItem);
        this.props.loadList(this.props.todoList);

        var neww = JSON.parse(JSON.stringify(this.props.curr));

        this.props.jtps.addTransaction(new transaction(old, neww, this.props.curr));

    }

    render() {
        return (
            <div id="todo_edit">
                <div id="item_heading" class="padding"><b>ITEM</b></div>
                <div id="item_form_container">
                    <table>
                        <col width="10%" />
                        <col width="90%" />
                        <tr>
                            <td class="padding"><b>Description:</b></td>
                            <td class="padding">
                                <input type="text" id="item_description_textfield" defaultValue={this.props.currentItem.description} />
                            </td>
                        </tr>
                        <tr>
                            <td class="padding"><b>Assign To:</b></td>
                            <td class="padding">
                                <input type="text" id="item_assigned_to_textfield" defaultValue={this.props.currentItem.assigned_to} />
                            </td>
                        </tr>
                        <tr>
                            <td class="padding"><b>Due Date:</b></td>
                            <td class="padding">
                                <input type="date" id="item_due_date_picker" defaultValue={this.props.currentItem.due_date} />
                            </td>
                        </tr>
                        <tr>
                            <td class="padding"><b>Completed:</b></td>
                            <td class="padding">
                                <input type="checkbox" id="item_completed_checkbox" defaultChecked={this.props.currentItem.completed} />
                            </td>
                        </tr>
                    </table>
                    <div>
                        <input type="button" id="item_form_submit_button" value="Submit" onClick={() => this.edit2List()} />
                        <input type="button" id="item_form_cancel_button" value="Cancel" onClick={() => this.go2List()} />
                    </div>
                </div>
            </div>

        )
    }
}

ItemScreen.propTypes = {
    currentScreen: PropTypes.string.isRequired,
    todoItem: PropTypes.object.isRequired
}

export default ItemScreen
