import React, { Component } from 'react'
import ListHeading from './ListHeading'
import ListItemsTable from './ListItemsTable'
import ListTrash from './ListTrash'
import PropTypes from 'prop-types';

import ListItemCard from './ListItemCard';
import transaction from '../../lib/transaction';


export class ListScreen extends Component {
    getListName() {
        if (this.props.todoList) {
            let name = this.props.todoList.name;
            return this.props.todoList.name;
        }
        else
            return "";
    }

    setListName() {
        var old = JSON.parse(JSON.stringify(this.props.curr));
        // if (this.props.undo.length == 0) {
        //     const copy = JSON.parse(JSON.stringify(this.props.homeList));
        //     this.props.undo.push(copy);
        // }
        //// push for undo
        // var copy = JSON.parse(JSON.stringify(this.props.homeList));
        // this.props.undo.push(copy);

        // console.log(this.props.undo);
        // alert();
        ////
        var tfName = document.getElementById('list_name_textfield').value;
        //alert(tfName);
        this.props.todoList.name = tfName;

        //var copy = JSON.parse(JSON.stringify(this.props.homeList));
        //var copy = this.props.homeList.map(a => Object.assign({}, a));
        var neww = JSON.parse(JSON.stringify(this.props.curr));

        this.props.jtps.addTransaction(new transaction(old, neww, this.props.curr));


        // console.log(this.props.undo);

    }

    setOwnerName() {
        var old = JSON.parse(JSON.stringify(this.props.curr));

        var tfOwner = document.getElementById('list_owner_textfield').value;
        //alert(tfName);
        this.props.todoList.owner = tfOwner;

        var neww = JSON.parse(JSON.stringify(this.props.curr));

        this.props.jtps.addTransaction(new transaction(old, neww, this.props.curr));
    }
    getListOwner() {
        if (this.props.todoList) {
            let owner = this.props.todoList.owner;
            return this.props.todoList.owner;
        }
    }
    render() {
        console.log('current big list  ' + this.props.homeList[0].name)
        return (
            <div id="todo_list"
            >
                <ListHeading goHome={this.props.goHome} />
                <ListTrash delete={this.props.delete}
                    homeList={this.props.homeList}
                    currListName={this.props.todoList.name}

                    goHomeFunc={this.props.goHomeDelete}
                />
                <div id="list_details_container">
                    <div id="list_details_name_container" className="text_toolbar">
                        <span id="list_name_prompt">Name:</span>
                        <input
                            defaultValue={this.getListName()}
                            type="text"
                            id="list_name_textfield"
                            onChange={() => this.setListName()}
                        />
                    </div>
                    <div id="list_details_owner_container" className="text_toolbar">
                        <span id="list_owner_prompt">Owner:</span>
                        <input
                            defaultValue={this.getListOwner()}
                            type="text"
                            id="list_owner_textfield"
                            onChange={() => this.setOwnerName()}
                        />
                    </div>
                </div>
                <ListItemsTable todoList={this.props.todoList}
                    loadFunc={this.props.loadList}
                    loadItem={this.props.loadItem}
                    editItem={this.props.editItem}

                    undo={this.props.undo}
                    redo={this.props.redo}
                    homeList={this.props.homeList}
                    curr={this.props.curr}
                    action={this.props.action}
                    jtps={this.props.jtps} />
            </div>
        )
    }
}

export default ListScreen
